<template>
   <div>
       <v-row
        no-gutters
       >
           <v-col cols="3"  class="hidden-sm-and-down">
               <app-user
                         :fetchOrders="fetchOrders"
                          :orders="orders"
                          :auth="auth"
                          :currentOrder="currentOrder"
               ></app-user>
           </v-col>
           <v-col cols="12" xs="12" sm="12" md="6" lg="6">
              <v-toolbar v-if="$route.name !== 'Message'" class=" hidden-md-and-up">
                  <v-toolbar-items class="mx-auto">
                      <v-btn @click="userMobileFunc"
                             :style=" userMobile ? `text-decoration: underline` : ``" text
                             class=" hidden-md-and-up">Users
                      </v-btn>
                      <v-btn
                              @click="messageMobileFunc"
                              :style=" messageMobile ? `text-decoration: underline` : ``" text
                              class=" hidden-md-and-up">Message
                      </v-btn>
                      <v-btn   @click="statusMobileFunc"
                              :style=" statusMobile ? `text-decoration: underline` : ``" text
                               class=" hidden-md-and-up">Status
                      </v-btn>
                  </v-toolbar-items>
              </v-toolbar>
               <router-view  class="hidden-sm-and-down"></router-view>
               <router-view class="hidden-md-and-up" v-if="messageMobile"/>
                   <app-user
                           class="hidden-md-and-up"
                           v-if="$route.name === 'Message' || userMobile"
                           :fetchOrders="fetchOrders"
                           :orders="orders"
                           :auth="auth"
                           :currentOrder="currentOrder"
                           :messageMobileFunc="messageMobileFunc"
                   ></app-user>
                   <app-status
                           class="hidden-md-and-up"
                           v-if="$route.name !== 'Message' && statusMobile "
                           :currentOrder="currentOrder"
                           :auth="auth"
                   ></app-status>
           </v-col>
           <v-col cols="3" class="hidden-sm-and-down">
               <app-status
                       :currentOrder="currentOrder"
                       :auth="auth"
               ></app-status>
           </v-col>
       </v-row>
<!--       <div class="hidden-md-and-up">-->
<!--           <v-img src="https://i.pinimg.com/originals/65/9c/fe/659cfe8fc0d9b168b721e90690e464c3.png"></v-img>-->
<!--       </div>-->
   </div>
</template>

<script>
    import appUser from '../../components/message/User'
    import appStatus from '../../components/message/Status'
    import { mapActions, mapGetters } from 'vuex'
    export default {
        components: {
            appUser,
            appStatus
        },
        data: () => ({
           orders: [],
            userMobile: false,
            messageMobile: true,
            statusMobile: false
        }),
        computed: {
          ...mapGetters([
              'auth',
              'currentOrder'
          ])
        },
        methods: {
            ...mapActions([
                'fetchCurrentOrder'
            ]),
             async fetchOrders () {
             const results = await this.$axios.get('order/orders', { params: {
                 authId: this.auth._id
                 }})
                 this.orders =  results.data.orders
            },
            userMobileFunc () {
                this.userMobile = true
                this.messageMobile = false
                this.statusMobile = false
            },
            messageMobileFunc () {
                this.messageMobile = true
                this.userMobile = false
                this.statusMobile = false
            },
            statusMobileFunc () {
                this.statusMobile = true
                this.userMobile = false
                this.messageMobile = false
            }
        },
       created () {
            this.fetchOrders()
            this.fetchCurrentOrder(this.$route.params.id)
        },
        mounted () {
           // const { _id, employerId } = this.currentOrder
                this.$io.on('update-quotation', () => this.fetchCurrentOrder(this.$route.params.id))
                this.$io.on('create-order', () => this.fetchOrders())
                this.$io.on('review', () => this.fetchCurrentOrder(this.$route.params.id))
            }
    }
</script>

