<template>
    <v-row justify="center">
        <v-btn
                :class="`success`"
                dark
                block
                @click.stop="dialog = true"
        >
          Review
        </v-btn>

        <v-dialog
                v-model="dialog"
                max-width="290"
        >
            <v-card>
                <v-card-title class="headline justify-center">
                  ໃຫ້ຄະແນນ   {{ rating }}
                </v-card-title>

                <v-card-text>
                   <v-form @submit.prevent="createReview">
                       <v-text-field v-model="text" label="ຄວາມຄິດເຫັນ"></v-text-field>
                       <div class="text-center">
                           <v-rating
                                   half-increments
                                   v-model="rating"
                                   color="yellow darken-3"
                                   background-color="grey darken-1"
                                   hover
                                   size="32"
                           ></v-rating>
                       </div>
                   </v-form>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn
                            color="error darken-1"
                            text
                            @click="dialog = false"
                    >
                        ຍົກເລີກ
                    </v-btn>

                    <v-btn
                            type="submit"
                            color="success"
                            text
                            @click="createReview"
                    >
                        ຕົກລົງ
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>


<script>
    import { mapActions } from 'vuex'
    export default {
        props: [
          'currentOrder',
            'auth'
        ],
        data: () => ({
                dialog: false,
                text: '',
                rating: null
        }),
        methods: {
            ...mapActions([
               'fetchCurrentOrder'
            ]),
           async createReview () {
              await this.$axios.post('review/post', {
                   text: this.text,
                   rating: this.rating,
                   portfolioId: this.currentOrder.portfolioId._id,
                   orderId: this.currentOrder._id,
                   userId: this.auth._id
               })
              await this.fetchCurrentOrder(this.currentOrder._id)
               this.$io.emit('review')
               this.dialog = false
           }
        }
    }
</script>
