<template>
    <v-row justify="center">
        <v-btn
                :class="`warning`"
                dark
                block
                @click.stop="dialog = true"
        >
            ແກ້ໄຂ Review
        </v-btn>
        <v-dialog
                v-model="dialog"
                max-width="290"
        >
            <v-card>
                <v-card-title class="headline justify-center">
                    ໃຫ້ຄະແນນ   {{ rating }}
                </v-card-title>

                <v-card-text>
                    <v-form @submit.prevent="updateReview">
                        <v-text-field v-model="text" label="ຄວາມຄິດເຫັນ"></v-text-field>
                        <div class="text-center">
                            <v-rating
                                    v-model="rating"
                                    half-increments
                                    color="yellow darken-3"
                                    background-color="grey darken-1"
                                    hover
                                    size="32"
                            ></v-rating>
                        </div>
                    </v-form>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>

                    <v-btn
                            color="error darken-1"
                            text
                            @click="dialog = false"
                    >
                        ຍົກເລີກ
                    </v-btn>

                    <v-btn
                            type="submit"
                            color="success"
                            text
                            @click="updateReview"
                    >
                        ຕົກລົງ
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>


<script>
    import { mapActions } from 'vuex'
    export default {
        props: [
            'currentOrder'
        ],
        data: () => ({
            dialog: false,
            text: '',
            rating: null
        }),
        methods: {
            ...mapActions([
                'fetchCurrentOrder'
            ]),
            async updateReview () {
                await this.$axios.put('review/update-review', {
                    reviewId: this.currentOrder.reviewId._id ,
                    text: this.text,
                    rating: this.rating,
                })
                await this.fetchCurrentOrder(this.currentOrder._id)
                this.$io.emit('review')
                this.dialog = false
            }
        },
    }
</script>
